.App {
  text-align: center;
}

.App-logo {
  height: 150px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 7s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo{
  background-color: #282c34;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.each-item{
  border: 1px solid rgb(175, 175, 175);
  border-radius: 4px;
  margin-left: 45px;
  padding-left:30px;
  padding-right:30px;
  color:purple;
  box-shadow: 4px 4px black;
  min-width:110px;
}

.list-table{
  display: flex;
  justify-content: center;
  align-items: center;  
}

footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: rgb(175, 175, 175);
  text-align: center;
  padding-bottom: 20px;
}

.menus{
  margin:10px;
}
.footer-link{
  display: flex;
  justify-content: center;
  align-items: center;  
}
